














































































































































import Vue from 'vue';
import EnemyInput from './EnemyInput.vue';
import AntiAirCalculator from '../result/AntiAirCalculator.vue';
import ContactRates from '../result/ContactRates.vue';
import EnemyFleet from '@/classes/enemy/enemyFleet';
import Enemy from '@/classes/enemy/enemy';

export default Vue.extend({
  components: {
    EnemyInput,
    AntiAirCalculator,
    ContactRates,
  },
  name: 'EnemyDetail',
  props: {
    handleShowItemList: {
      type: Function,
    },
    fleet: {
      type: EnemyFleet,
      required: true,
    },
    handleClose: {
      type: Function,
      required: true,
    },
  },
  data: () => ({
    tab: 'stage2',
  }),
  computed: {
    enemies(): Enemy[] {
      return this.fleet.enemies.filter((v) => v.data.id > 0);
    },
    mainEnemies(): Enemy[] {
      return this.fleet.enemies.filter((v) => v.data.id > 0 && !v.isEscort);
    },
    escorts(): Enemy[] {
      return this.fleet.enemies.filter((v) => v.data.id > 0 && v.isEscort);
    },
  },
  methods: {
    showItemList(enemyIndex: number, slotIndex: number): void {
      // 敵の装備変更
      this.handleShowItemList(enemyIndex, slotIndex);
    },
    close() {
      this.handleClose();
    },
  },
});
